@media (min-width: 200px) {
  /* smartphones, portrait iPhone, portrait 480x320 phones (Android) */
  h1 {
    font-size: 28px;
  }
  h2 {
    font-size: 23px;
  }
  h3 {
    font-size: 19px;
  }
  h4,
  li {
    font-size: 15px;
  }
  h5 {
    font-size: 12px;
  }
  .img-container .list-group-item {
    font-size: 11px;
    padding: 10px 10px;
  }
}
@media (min-width: 480px) {
  /* smartphones, Android phones, landscape iPhone */
  h1 {
    font-size: 28px;
  }
  h2 {
    font-size: 23px;
  }
  h3 {
    font-size: 19px;
  }
  h4,
  li {
    font-size: 15px;
  }
  h5 {
    font-size: 12px;
  }

  .img-container .list-group-item {
    font-size: 12px;
    padding: 10px 10px;
  }
}
@media (min-width: 600px) {
  /* portrait tablets, portrait iPad, e-readers (Nook/Kindle), landscape 800x480 phones (Android) */
  h1 {
    font-size: 35px;
  }
  h2 {
    font-size: 28px;
  }
  h3 {
    font-size: 19px;
  }
  h4,
  li {
    font-size: 17px;
  }
  h5 {
    font-size: 14px;
  }
  .img-container .list-group-item {
    font-size: 14px;
    padding: 10px 10px;
  }
}
@media (min-width: 801px) {
  /* tablet, landscape iPad, lo-res laptops ands desktops */
  h1 {
    font-size: 35px;
  }
  h2 {
    font-size: 28px;
  }
  h3 {
    font-size: 21px;
  }
  h4,
  li {
    font-size: 17px;
  }
  h5 {
    font-size: 14px;
  }
  .img-container .list-group-item {
    font-size: 14px;
    padding: 10px 10px;
  }
}
@media (min-width: 1025px) {
  /* big landscape tablets, laptops, and desktops */
  h1 {
    font-size: 42px;
  }
  h2 {
    font-size: 32px;
  }
  h3 {
    font-size: 25px;
  }
  h4,
  li {
    font-size: 19px;
  }
  h5 {
    font-size: 15px;
  }
  .img-container .list-group-item {
    font-size: 15px;
    padding: 10px 10px;
  }
}
@media (min-width: 1281px) {
  /* hi-res laptops and desktops */
  h1 {
    font-size: 42px;
  }
  h2 {
    font-size: 32px;
  }
  h3 {
    font-size: 25px;
  }
  h4,
  li {
    font-size: 19px;
  }
  h5 {
    font-size: 18px;
  }
  .img-container .list-group-item {
    font-size: 18px;
    padding: 10px 10px;
  }
}

.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

nav.top-nav {
  padding: 20px;
  position: relative;
  display: block;
  float: left;
  width: 100%;
  background-color: #c0c0af;
}

.logo {
  position: relative;
  display: block;
  float: left;
  font-size: 17px !important;
  font-family: "Homemade Apple", cursive;
  color: #3d3d3d;
}

div.navigation-links {
  position: relative;
  display: block;
  float: right;
}

.navigation-links ul {
  position: relative;
  display: block;
  float: left;
}

ul li.nav-link {
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 0px;
  padding-right: 8px;
}

div ul li {
  position: relative;
  display: block;
  float: left;
  margin: 0px 1px;
}

li.nav-link a {
  color: #3d3d3d;
  text-decoration: none;
  font-size: 15px;
  transition: all 0.2s ease-in-out;
  text-transform: uppercase;
  font-family: "Cuprum", sans-serif;
  font-weight: 600;
}

li.nav-link a:hover {
  font-size: 16px;
  color: white;
  opacity: 0.8;
  border-bottom: 3px solid #a5a58d;
}

li.nav-link .active {
  color: white;
  border-bottom: 3px solid #a5a58d;
}

.menuToggle {
  position: relative;
  top: 15px;
  left: 15px;
  width: 35px;
  z-index: 1;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

.menuToggle input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none;
}

.menuToggle span {
  display: block;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #656550;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

.menuToggle span:nth-of-type(1) {
  width: 50%;
}

.menuToggle span:nth-of-type(2) {
  width: 100%;
}

.menuToggle span:nth-of-type(3) {
  width: 75%;
}

.menuToggle span:first-child {
  transform-origin: 0% 0%;
}

.menuToggle span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

#menuToggle input:checked {
  left: 125px;
}

#menuToggle input:checked ~ span {
  opacity: 1;
  left: 125px;
  transform: rotate(-45deg) translate(-12px, 1px);
  background: #8e8e71;
  width: 70%;
}

#menuToggle input:checked ~ span:nth-of-type(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

#menuToggle input:checked ~ span:nth-of-type(2) {
  left: 125px;
  transform: rotate(45deg) translate(-12px, -5px);
  width: 70%;
}

.menu {
  position: absolute;
  width: 200px;
  min-height: 100vh;
  margin: -42px 0 0 -50px;
  padding-left: 55px;
  padding-bottom: 20px;
  padding-top: 5px;
  background: #c0c0af;
  -webkit-font-smoothing: antialiased;
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

.menu li {
  padding: 10px 0;
  margin-right: 150px;
  font-size: 20px;
}

.menuToggle input:checked ~ ul {
  transform: none;
}

.social-links-div {
  float: right;
  padding-right: 20px;
  margin-top: -10px;
}

.icon-holder {
  list-style: none;
}

svg {
  text-decoration: none;
  margin-left: 2px;
  margin-right: 2px;
  color: #656550;
  font-size: 16px !important;
  transition: ease-in-out 0.2s;
}

svg:hover {
  color: #76765d;
  font-size: 19px !important;
}

footer {
  background: #c0c0af;
  bottom: 0 !important;
  width: 100%;
  padding: 40px 10px;
  position: relative;
  font-family: "Cuprum", sans-serif;
  display: flex;
  justify-content: center;
  text-align: center;
}

footer a {
  cursor: pointer;
  color: #656550;
  font-size: 14px;
  transition: ease-in-out 0.2s;
}

footer a:hover {
  color: #76765d;
  opacity: 0.8;
  text-decoration: none;
  font-size: 15px;
}

h5.copyright-text {
  font-size: 14px;
  color: #656550;
  padding-top: 20px;
}

ul.footer-nav,
ul.footer-icons {
  display: flex;
  justify-content: center;
  align-items: center;
}
ul.footer-nav li {
  margin: 0px;
  padding: 10px;
  left: -12px;
}

ul.footer-icons li {
  margin: 0px;
  left: -15px;
  padding: 6px;
}

.footer-btn {
  text-transform: uppercase;
  border: none;
  background: transparent;
  color: #656550;
  transition: ease-in-out 0.3;
  font-size: 14px;
}

.footer-btn:hover {
  color: white;
  font-size: 15px;
}

.container {
  padding: 20px;
}




